import React from "react";
import Layout from '../components/Layouts/Layout'

const About = () => {
  return (
    <Layout title={"About us - Ecommerce app"}>
      <div className="row contactus ">
        <div className="col-md-6 ">
          <img
            src="/images/about.jpeg"
            alt="contactus"
            style={{ width: "100%" }}
          />
        </div>
        <div className="col-md-4">
          <p className="text-justify mt-2">
          Welcome to our 
          e-commerce, your ultimate online shopping destination. Discover a diverse range of high-quality products, enjoy personalized recommendations, and experience seamless browsing. With exceptional customer service, user-friendly interface, and philanthropic initiatives, we strive to make your shopping journey effortless and meaningful. Start exploring Shopit today and redefine your online shopping experience.
          </p>
        </div>
      </div>
    </Layout>
  );
};

export default About;